:root {
  --surface-color: $white;
  --curve: 40;
}

* {
  box-sizing: border-box;
}

h2 {
  font-size: 1.4em;
  text-align: center;
}

a {
  color: #4c5e85;
}

button {
  border: none;
  margin: 5px;
}

body {
  background-color: #e2eafc;
}

.img {
  width: 100%;
  height: auto;
}

.btn-primary {
  background-color: #6a82b7 !important;
  border-color: #6a82b7 !important;
}
.btn-primary:hover {
  background-color: #4c5e85 !important;
  border-color: #4c5e85 !important;
}

.btn-outline-primary {
  color: #6a82b7 !important;
  border-color: #6a82b7 !important;
}
.btn-outline-primary:hover {
  color: white !important;
  background-color: #6a82b7 !important;
}

.button {
  background-color: #4c5e85 !important;
  border-color: #4c5e85 !important;
}
.button:hover {
  background-color: #e2eafc !important;
  border-color: #e2eafc !important;
}

.button-group {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

.card {
  color: #4a3e42;
  margin-bottom: 2rem;
}

.card-header {
  padding: 2em;
  background-color: transparent;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  max-height: 250px;
}

.card-body {
  color: #4a3e42;
  padding: 1rem 2rem 2rem;
}

.card-footer {
  text-align: center;
  background-color: white;
  border: none;
}